"use client"

import { useState } from "react"
import { SiteHeader } from "@/components/site-header"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { AlertCircle, CheckCircle, Clock, Globe, Shield, Activity, Search } from "lucide-react"

type PageStatus = "healthy" | "warning" | "error"

interface PageResult {
    url: string
    status: PageStatus
    statusCode: number
    loadTime: number
    issues: string[]
}

interface CheckResult {
    main: PageResult
    pages: PageResult[]
    scannedCount: number
}

export default function HealthCheckerPage() {
    const [url, setUrl] = useState("")
    const [loading, setLoading] = useState(false)
    const [result, setResult] = useState<CheckResult | null>(null)
    const [error, setError] = useState("")

    const handleCheck = async (e: React.FormEvent) => {
        e.preventDefault()
        if (!url) return

        setLoading(true)
        setError("")
        setResult(null)

        try {
            const res = await fetch("/api/health-check", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ url }),
            })

            const data = await res.json()

            if (!res.ok) {
                throw new Error(data.error || "Failed to check website")
            }

            setResult(data)
        } catch (err: any) {
            setError(err.message)
        } finally {
            setLoading(false)
        }
    }

    const getStatusColor = (status: PageStatus) => {
        switch (status) {
            case "healthy": return "text-green-500 bg-green-50 dark:bg-green-900/20 border-green-200 dark:border-green-900"
            case "warning": return "text-yellow-500 bg-yellow-50 dark:bg-yellow-900/20 border-yellow-200 dark:border-yellow-900"
            case "error": return "text-red-500 bg-red-50 dark:bg-red-900/20 border-red-200 dark:border-red-900"
            default: return "text-slate-500 bg-slate-50"
        }
    }

    const getStatusIcon = (status: PageStatus) => {
        switch (status) {
            case "healthy": return <CheckCircle className="w-5 h-5 text-green-500" />
            case "warning": return <AlertCircle className="w-5 h-5 text-yellow-500" />
            case "error": return <AlertCircle className="w-5 h-5 text-red-500" />
            default: return <Activity className="w-5 h-5" />
        }
    }

    return (
        <main className="min-h-screen bg-slate-50 dark:bg-slate-950">
            <SiteHeader />

            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
                <div className="mb-8">
                    <h1 className="text-3xl font-bold text-slate-900 dark:text-white mb-2">Website Health Checker</h1>
                    <p className="text-slate-500 dark:text-slate-400">
                        Scan your website for dead links, slow pages, security issues, and comprehensive health status.
                    </p>
                </div>

                {/* Search Input */}
                <Card className="mb-8 border-none shadow-lg">
                    <CardContent className="p-6">
                        <form onSubmit={handleCheck} className="flex gap-4 flex-col sm:flex-row">
                            <div className="flex-1 relative">
                                <Globe className="absolute left-3 top-3 h-5 w-5 text-slate-400" />
                                <Input
                                    type="text"
                                    placeholder="Enter website URL (e.g., https://example.com)"
                                    className="pl-10 h-11 text-lg"
                                    value={url}
                                    onChange={(e) => setUrl(e.target.value)}
                                />
                            </div>
                            <Button type="submit" disabled={loading} className="h-11 px-8 text-lg bg-blue-600 hover:bg-blue-700">
                                {loading ? (
                                    <>
                                        <Activity className="mr-2 h-5 w-5 animate-spin" />
                                        Scanning...
                                    </>
                                ) : (
                                    <>
                                        <Search className="mr-2 h-5 w-5" />
                                        Check Health
                                    </>
                                )}
                            </Button>
                        </form>
                        {error && (
                            <div className="mt-4 p-4 text-sm text-red-500 bg-red-50 dark:bg-red-900/20 rounded-lg flex items-center">
                                <AlertCircle className="w-5 h-5 mr-2" />
                                {error}
                            </div>
                        )}
                    </CardContent>
                </Card>

                {result && (
                    <div className="space-y-6">
                        {/* Overview Cards */}
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                            <Card>
                                <CardHeader className="flex flex-row items-center justify-between pb-2">
                                    <CardTitle className="text-sm font-medium">Overall Status</CardTitle>
                                    <Activity className="h-4 w-4 text-slate-500" />
                                </CardHeader>
                                <CardContent>
                                    <div className="text-2xl font-bold flex items-center gap-2">
                                        {getStatusIcon(result.main.status)}
                                        <span className="capitalize">{result.main.status}</span>
                                    </div>
                                    <p className="text-xs text-slate-500 mt-1">
                                        Main page status
                                    </p>
                                </CardContent>
                            </Card>

                            <Card>
                                <CardHeader className="flex flex-row items-center justify-between pb-2">
                                    <CardTitle className="text-sm font-medium">Pages Scanned</CardTitle>
                                    <CheckCircle className="h-4 w-4 text-slate-500" />
                                </CardHeader>
                                <CardContent>
                                    <div className="text-2xl font-bold">{result.pages.length}</div>
                                    <p className="text-xs text-slate-500 mt-1">
                                        Menu and header links
                                    </p>
                                </CardContent>
                            </Card>

                            <Card>
                                <CardHeader className="flex flex-row items-center justify-between pb-2">
                                    <CardTitle className="text-sm font-medium">Avg Load Time</CardTitle>
                                    <Clock className="h-4 w-4 text-slate-500" />
                                </CardHeader>
                                <CardContent>
                                    <div className="text-2xl font-bold">
                                        {Math.round(result.pages.reduce((acc, p) => acc + p.loadTime, 0) / result.pages.length)}ms
                                    </div>
                                    <p className="text-xs text-slate-500 mt-1">
                                        Average across all pages
                                    </p>
                                </CardContent>
                            </Card>
                        </div>

                        {/* Detailed Results */}
                        <Card>
                            <CardHeader>
                                <CardTitle>Detailed Report</CardTitle>
                                <CardDescription>Health status for individual pages found in the menu.</CardDescription>
                            </CardHeader>
                            <CardContent>
                                <div className="rounded-md border">
                                    <div className="relative w-full overflow-auto">
                                        <table className="w-full caption-bottom text-sm text-left">
                                            <thead className="[&_tr]:border-b">
                                                <tr className="border-b transition-colors hover:bg-slate-100/50 dark:hover:bg-slate-800/50 data-[state=selected]:bg-slate-100 dark:data-[state=selected]:bg-slate-800">
                                                    <th className="h-12 px-4 align-middle font-medium text-slate-500 dark:text-slate-400">URL</th>
                                                    <th className="h-12 px-4 align-middle font-medium text-slate-500 dark:text-slate-400">Status</th>
                                                    <th className="h-12 px-4 align-middle font-medium text-slate-500 dark:text-slate-400">Load Time</th>
                                                    <th className="h-12 px-4 align-middle font-medium text-slate-500 dark:text-slate-400">Issues</th>
                                                </tr>
                                            </thead>
                                            <tbody className="[&_tr:last-child]:border-0">
                                                {result.pages.map((page, i) => (
                                                    <tr key={i} className="border-b transition-colors hover:bg-slate-100/50 dark:hover:bg-slate-800/50">
                                                        <td className="p-4 align-middle">
                                                            <a href={page.url} target="_blank" rel="noreferrer" className="font-medium text-blue-600 hover:underline max-w-[300px] truncate block">
                                                                {page.url}
                                                            </a>
                                                        </td>
                                                        <td className="p-4 align-middle">
                                                            <Badge variant="outline" className={`${getStatusColor(page.status)}`}>
                                                                {page.status}
                                                            </Badge>
                                                        </td>
                                                        <td className="p-4 align-middle">
                                                            <span className={page.loadTime > 3000 ? "text-yellow-600 font-medium" : ""}>
                                                                {page.loadTime}ms
                                                            </span>
                                                        </td>
                                                        <td className="p-4 align-middle">
                                                            {page.issues.length > 0 ? (
                                                                <ul className="list-disc list-inside text-xs text-red-500">
                                                                    {page.issues.map((issue, idx) => (
                                                                        <li key={idx}>{issue}</li>
                                                                    ))}
                                                                </ul>
                                                            ) : (
                                                                <span className="text-green-500 text-xs flex items-center">
                                                                    <CheckCircle className="w-3 h-3 mr-1" /> No issues found
                                                                </span>
                                                            )}
                                                        </td>
                                                    </tr>
                                                ))}
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </CardContent>
                        </Card>
                    </div>
                )}
            </div>
        </main>
    )
}
